:root {
  --font-family-primary: "Vollkorn", serif;
  --font-family-secondary: "PT Sans", sans-serif;
  --font-size-title: 32px;
  --line-height-title: 1.4;
  --font-size-caption: 14px;
  --line-height-caption: 1.2;
  --color-text: #222022;
  --color-highlight-primary: #ffef7e;
  --color-highlight-secondary: #b7f9e9;
  --border-radius-primary: 32px; }

@media (min-width: 576px) {
  :root {
    --font-size-title: 40px; } }

.card {
  max-width: 960px;
  border-radius: var(--border-radius-primary);
  box-shadow: 24px 24px 80px rgba(0, 0, 0, 0.1);
  padding: 20px 20px 28px 20px;
  box-sizing: border-box;
  margin: 20px;
  display: flex;
  flex-direction: column; }
  @media (min-width: 576px) {
    .card {
      flex-direction: row;
      align-items: center;
      margin: 40px;
      padding: 32px; } }

.card__image {
  width: 100%;
  max-height: 300px;
  border-radius: var(--border-radius-primary);
  object-fit: cover;
  margin-bottom: 18px; }
  @media (min-width: 576px) {
    .card__image {
      width: 45%;
      max-height: none;
      min-height: 400px;
      margin-bottom: 0; } }

@media (min-width: 576px) {
  .card__content {
    width: 55%;
    padding-left: 40px; } }

.card__date {
  display: block;
  font-family: var(--font-family-secondary);
  font-size: var(--font-size-caption);
  line-height: var(--line-height-caption);
  text-transform: uppercase;
  color: var(--color-text);
  margin-bottom: 6px; }
  @media (min-width: 576px) {
    .card__date {
      margin-bottom: 8px; } }

.card__title {
  font-family: var(--font-family-primary);
  font-size: var(--font-size-title);
  line-height: var(--line-height-title);
  color: var(--color-text);
  box-decoration-break: clone;
  background-image: linear-gradient(90deg, var(--color-highlight-primary), var(--color-highlight-secondary));
  background-size: 100% 42%;
  background-repeat: no-repeat;
  background-position: 0 85%;
  padding: 0 4px;
  margin-left: -4px; }

.card_button {
  margin-left: 80%;
  background-color: #e0dcd6;
  color: #747272;
  border: none;
  border-radius: 10px;
  box-shadow: 0px 0px 2px 2px #d8d6d6;
  min-height: 30px;
  min-width: 100px;
  cursor: pointer; }

.card_button:hover {
  background-color: #ebcc66;
  transition: 0.5s; }
